import axios from 'axios';
import React, { useEffect, useState } from 'react';

import DetailsVerification from '../../components/details-verification';
import OtpVerification from '../../components/otp-verification';
import { docTypes } from '../../config/enums';
import { getUrlParameter, isJson, isSafari } from '../../config/helpers';
import '@prototypeinteractive/jawwy-ui-kit-react/lib/index.css';
import arTranslation from '../../data/ar.json';
import enTranslation from '../../data/en.json';
import Loading from '../../loading.png';

import {
    Container, GlobalStyle, Card, StyledLogo, StyledLinksContainer, StyledButton, Overlay, StyledLoading
} from './styles';

const baseApi = process.env.REACT_APP_API;
const pathPrefix = process.env.PUBLIC_URL || '/';

const Download = () => {
    const [ activeScreen, setActiveScreen ] = useState(1);
    const [ docType, setDocType ] = useState(null);
    const [ altMobileNumber, setAltMobileNumber ] = useState(null);
    const [ documentId, setDocumentId ] = useState('');
    const [ nationalId, setNationalId ] = useState('');
    const [ mobileNumber, setMobileNumber ] = useState(null);
    const [ otpStatus, setOtpStatus ] = useState('initial');
    const [ errorMessage, setErrorMessage ] = useState('');
    const [ isLoading, setIsLoading ] = useState(false);
    const [ transactionId, setTransactionId ] = useState('');
    const [ dmsDocumentId, setDmsDocumentId ] = useState('');
    const [ lang, setLang ] = useState('ar');
    const [ translation, setTranslation ] = useState(arTranslation);
    const [ firstLoad, setFirstLoad ] = useState(true);
    const [ apiResponse, setApiResponse ] = useState({});

    useEffect(() => {
        const isAr = lang !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        const stateObj = { id: '101' };

        if (!firstLoad) {
            let queryParam = window.location.search;

            if (isAr) {
                queryParam = queryParam.replace('lang=en&', '');
                queryParam = queryParam.replace('lang=en', '');
                window.history.replaceState(stateObj,
                    'Download Document', queryParam === '?' ? pathPrefix : `${pathPrefix}${queryParam}`);
            } else {
                queryParam = queryParam.replace('?', '&');

                if (!queryParam.includes('lang=en')) {
                    window.history.replaceState(stateObj,
                        'Download Document', `${pathPrefix}?lang=en${queryParam}`);
                }
            }
        }
    }, [ lang, firstLoad ]);

    useEffect(() => {
        const newDocumentId = getUrlParameter('doc');
        const documentType = getUrlParameter('docType');
        const mainMobileNumber = getUrlParameter('mn');
        const alternativeMobileNumber = getUrlParameter('altmn');
        const allDocTypes = Object.values(docTypes);

        setDocumentId(newDocumentId);

        if (allDocTypes.includes(documentType?.toUpperCase()))
            setDocType(documentType?.toUpperCase());
        else
            setDocType(docTypes.Contract);

        setMobileNumber(mainMobileNumber?.replace('966', ''));
        setAltMobileNumber(alternativeMobileNumber?.replace('966', ''));

        const isAr = getUrlParameter('lang') !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        setFirstLoad(false);
    }, []);

    useEffect(() => {
        setApiResponse({});
    }, [ nationalId, mobileNumber ]);

    const handleDetailsSubmission = () => {
        const mainMobileNumber = mobileNumber ? `966${mobileNumber}` : null;

        const requestBody = {
            mobile_number: docType === docTypes.Device ? `966${altMobileNumber}` : mainMobileNumber,
            alt_mobile_number: `966${altMobileNumber}`,
            doc_type: docType || null,
            customer_id: nationalId,
            jawwy_document_id: documentId,
            language: lang
        };
        setIsLoading(true);
        setErrorMessage('');
        axios.post(`${baseApi}/dms/validate-document`, requestBody).then((res) => {
            setIsLoading(false);

            if (res.data.status === 'success') {
                setTransactionId(res.data.transaction_id);
                setDmsDocumentId(res.data.dms_document_id);
                setActiveScreen(2);
            } else if (![ 'not_found', 'Invalid_ID_Number' ].includes(res.data.error))
                throw new Error();

            setApiResponse(res.data);
        }).catch((error) => {
            setErrorMessage('default_error');
            setIsLoading(false);
            console.log(error);
        });
    };

    const resendOTP = () => {
        const requestBody = {
            transaction_id: transactionId
        };
        axios.post(`${baseApi}/dms/resend-otp`, requestBody).then((res) => {
            if (res.data.status === 'success')
                setOtpStatus('initial');
            else
                throw new Error();
        }).catch((error) => {
            setIsLoading(false);
            setErrorMessage('default_error');
            console.log(error);
        });
    };

    const handleOTPSubmission = (value, callback) => {
        if (value.length < 4)
            return;

        const requestBody = {
            otp: value,
            dms_document_id: dmsDocumentId,
            transaction_id: transactionId
        };

        setIsLoading(true);
        axios.post(`${baseApi}/dms/get-document`,
            requestBody,
            {
                responseType: 'arraybuffer',
                headers: { accept: '*/*' }
            })
            .then((res) => {
                setIsLoading(false);
                let decodedString = '';
                const bytes = new Uint8Array(res.data);
                const len = bytes.byteLength;

                for (let i = 0; i < len; i++)
                    decodedString += String.fromCharCode(bytes[i]);

                if (isJson(decodedString)) {
                    const obj = JSON.parse(decodedString);
                    const status = obj.status;
                    const message = obj.error_message;

                    if (status === 'failed') {
                        if ([ 'INVALID OTP.', 'OTP IS EXPIRED.' ].includes(message))
                            setErrorMessage('wrong_otp');
                        else
                            throw Error();

                        setOtpStatus('wrong');
                        callback(false);

                        return;
                    }
                }

                setOtpStatus('correct');
                const file = new Blob(
                    [ res.data ],
                    { type: 'application/pdf' }
                );
                const fileURL = URL.createObjectURL(file);

                if (window.navigator && window.navigator.msSaveOrOpenBlob) { // for IE
                    window.navigator.msSaveOrOpenBlob(file);
                } else if (isSafari) { // Safari & IOS
                    window.location.assign(fileURL);
                } else
                    window.open(fileURL);
            }).catch((error) => {
                setIsLoading(false);
                setOtpStatus('wrong');
                setErrorMessage('default_error');
                console.log(error);
            });
    };

    return (
        <>
            <Overlay display={isLoading} />
            <StyledLoading display={isLoading} src={Loading} />
            <GlobalStyle />
            <Container>
                <StyledLogo />
                <StyledLinksContainer flexEnd={activeScreen === 1}>
                    {activeScreen === 2 && (
                        <StyledButton
                            variant='link'
                            onClick={() => {
                                setOtpStatus('initial');
                                setActiveScreen(1);
                            }}
                        >
                            {translation.secondPage.backButton}
                        </StyledButton>
                    )}
                    <StyledButton
                        variant='link'
                        onClick={() => { setLang(lang === 'ar' ? 'en' : 'ar'); }}
                    >
                        {translation.lang}
                    </StyledButton>
                </StyledLinksContainer>
                <Card>
                    {activeScreen === 1 && (
                        <DetailsVerification
                            altMobileNumber={altMobileNumber}
                            apiResponse={apiResponse}
                            docType={docType}
                            errorMsg={errorMessage}
                            isLoading={isLoading}
                            mobileNumber={mobileNumber}
                            nationalId={nationalId}
                            setDocumentId={setDocumentId}
                            setMobileNumber={setMobileNumber}
                            setNationalId={setNationalId}
                            translation={translation}
                            onSubmit={handleDetailsSubmission}
                        />
                    )}
                    {activeScreen === 2 && (
                        <OtpVerification
                            altMobileNumber={altMobileNumber}
                            docType={docType}
                            mobileNumber={mobileNumber}
                            OTPMessage={errorMessage}
                            OTPStatus={otpStatus}
                            sendOtp={resendOTP}
                            setActiveScreen={setActiveScreen}
                            setOtpStatus={setOtpStatus}
                            translation={translation}
                            onChange={handleOTPSubmission}
                        />
                    )}
                </Card>
            </Container>
        </>
    );
};

export default Download;
