import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';

import {
    Description, TitleContainer, Title, Main, ButtonStyled, StyledCheckBox, FieldSeparator
} from './styles';

const BlockingOptions = ({
    service,
    serviceStatus,
    setNewService,
    setNewServiceStatus,
    translation,
    onSubmit,
    isLoading
}) => {
    const [ isButtonActive /* , setIsButtonActive */ ] = useState(false);
    const [ values, setValues ] = useState({
        block_international_calls: false,
        block_international_sms: false,
        block_all: false
    });

    useEffect(() => {
        if (serviceStatus === 'block') {
            setValues({
                ...values,
                block_international_calls: [ 'voice', 'smsAndVoice' ].includes(service),
                block_international_sms: [ 'sms', 'smsAndVoice' ].includes(service),
                block_all: service === 'smsAndVoice'
            });
        }
    }, [ serviceStatus, service ]);

    const handleChange = useCallback((event, field) => {
        const checked = event.target.checked;

        const newValues = {
            ...values,
            [field]: checked
        };

        switch (field) {
            case 'block_international_calls':
                if (!checked)
                    newValues.block_all = false;
                else
                    newValues.block_all = values.block_international_sms;

                break;

            case 'block_international_sms':
                if (!checked)
                    newValues.block_all = false;
                else
                    newValues.block_all = values.block_international_calls;

                break;

            case 'block_all':
                newValues.block_international_calls = checked;
                newValues.block_international_sms = checked;

                break;

            default:
        }

        if (newValues.block_international_calls && !newValues.block_international_sms)
            setNewService('voice');
        else if (newValues.block_international_sms && !newValues.block_international_calls)
            setNewService('sms');
        else if (newValues.block_all)
            setNewService('smsAndVoice');

        setNewServiceStatus(
            newValues.block_international_calls || newValues.block_international_sms || newValues.block_all ?
                'block' : 'unblock'
        );

        setValues(newValues);
    }, [ values ]);

    return (
        <Main>
            <Helmet>
                <title>
                    {translation.blockingOptions.title}
                </title>
            </Helmet>
            <TitleContainer>
                <Title>
                    {translation.blockingOptions.title}
                </Title>
            </TitleContainer>
            <Description>
                {translation.blockingOptions.description}
            </Description>
            <div>
                <StyledCheckBox
                    checked={!!values.block_international_calls}
                    variant='switch'
                    onChange={(e) => handleChange(e, 'block_international_calls')}
                >
                    {translation.blockingOptions.fields.block_international_calls.label}
                </StyledCheckBox>
                <FieldSeparator />
                <StyledCheckBox
                    checked={!!values.block_international_sms}
                    variant='switch'
                    onChange={(e) => handleChange(e, 'block_international_sms')}
                >
                    {translation.blockingOptions.fields.block_international_sms.label}
                </StyledCheckBox>
                <FieldSeparator />
                <StyledCheckBox
                    checked={!!values.block_all}
                    variant='switch'
                    onChange={(e) => handleChange(e, 'block_all')}
                >
                    {translation.blockingOptions.fields.block_all.label}
                </StyledCheckBox>
            </div>
            <ButtonStyled
                disabled={false && (!isButtonActive || isLoading)}
                onClick={onSubmit}
            >
                {translation.blockingOptions.buttonLabel}
            </ButtonStyled>
        </Main>
    );
};

BlockingOptions.propTypes = {
    isLoading: PropTypes.bool,
    onSubmit: PropTypes.func,
    service: PropTypes.oneOf([ '', 'sms', 'voice', 'smsAndVoice' ]),
    serviceStatus: PropTypes.oneOf([ 'none', 'block', 'unblock' ]),
    setNewService: PropTypes.func,
    setNewServiceStatus: PropTypes.func,
    translation: PropTypes.shape({
        // eslint-disable-next-line react/forbid-prop-types
        blockingOptions: PropTypes.object
    })
};

BlockingOptions.defaultProps = {
    isLoading: null,
    onSubmit: null,
    service: null,
    serviceStatus: null,
    setNewService: null,
    setNewServiceStatus: null,
    translation: null
};
export default BlockingOptions;
