import axios from 'axios';
import React, { useEffect, useState } from 'react';

import ConfirmTermination from '../../components/confirm-termination';
import OtpVerification from '../../components/otp-verification';
import TerminationFailure from '../../components/termination-failure';
import TerminationForm from '../../components/termination-form';
import TerminationSuccess from '../../components/termination-success';
import { getUrlParameter } from '../../config/helpers';
import '@prototypeinteractive/jawwy-ui-kit-react/lib/index.css';
import arTranslation from '../../data/ar.json';
import enTranslation from '../../data/en.json';
import Loading from '../../loading.png';

import {
    Container, GlobalStyle, Card, StyledLogo, StyledLinksContainer, StyledButton, Overlay, StyledLoading
} from './styles';

const baseApi = process.env.REACT_APP_API_LC;
const pathPrefix = process.env.PUBLIC_URL || '/termination-request';

const TerminationRequest = () => {
    const [ activeScreen, setActiveScreen ] = useState(1);
    const [ nationalId, setNationalId ] = useState('');
    const [ mobileNumber, setMobileNumber ] = useState('');
    const [ otpStatus, setOtpStatus ] = useState('initial');
    const [ errorMessage, setErrorMessage ] = useState('');
    const [ isLoading, setIsLoading ] = useState(false);
    const [ transactionId, setTransactionId ] = useState('');
    const [ lang, setLang ] = useState('ar');
    const [ translation, setTranslation ] = useState(arTranslation);
    const [ firstLoad, setFirstLoad ] = useState(true);
    const [ apiResponse, setApiResponse ] = useState({});

    useEffect(() => {
        const isAr = lang !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        const stateObj = { id: '101' };

        if (!firstLoad) {
            let queryParam = window.location.search;

            if (isAr) {
                queryParam = queryParam.replace('lang=en&', '');
                queryParam = queryParam.replace('lang=en', '');
                window.history.replaceState(stateObj,
                    'Download Document', queryParam === '?' ? pathPrefix : `${pathPrefix}${queryParam}`);
            } else {
                queryParam = queryParam.replace('?', '&');

                if (!queryParam.includes('lang=en')) {
                    window.history.replaceState(stateObj,
                        'Download Document', `${pathPrefix}?lang=en${queryParam}`);
                }
            }
        }
    }, [ lang, firstLoad ]);

    useEffect(() => {
        const isAr = getUrlParameter('lang') !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        setFirstLoad(false);
    }, []);

    useEffect(() => {
        setApiResponse({});
    }, [ nationalId, mobileNumber ]);

    const handleDetailsSubmission = () => {
        const requestBody = {
            msisdn: `966${mobileNumber}`,
            preferred_lng: lang,
            iqama_id_c: nationalId
        };
        setIsLoading(true);
        setErrorMessage('');
        axios.post(`${baseApi}/terminate-validate-msisdn`, requestBody).then((res) => {
            setIsLoading(false);

            if (res.data.status === 'success') {
                setTransactionId(res.data.transactionid);
                setActiveScreen(2);
            } else if (![ 'not_found', 'Invalid_ID_Number' ].includes(res.data.error_code))
                throw new Error();

            if ([ 'not_found', 'Invalid_ID_Number' ].includes(res.data.error_code)) {
                setErrorMessage('not_found');
                setIsLoading(false);
            } else
                setApiResponse(res.data);
        }).catch((error) => {
            setErrorMessage('default_error');
            setIsLoading(false);
            console.error(error);
        });
    };

    const resendOTP = () => {
        const requestBody = {
            caller_id: 'Portal',
            transaction_id: transactionId
        };
        setIsLoading(true);
        axios.post(`${baseApi}/resend-otp`, requestBody).then((res) => {
            setIsLoading(false);

            if (res.data.status === 'success')
                setOtpStatus('initial');
            else
                throw new Error();
        }).catch((error) => {
            setIsLoading(false);
            setErrorMessage('default_error');
            console.error(error);
        });
    };

    const handleOTPSubmission = (value, callback) => {
        if (value.length < 4)
            return;

        const requestBody = {
            caller_id: 'Portal',
            otp: value,
            transaction_id: transactionId
        };

        setIsLoading(true);

        axios.post(`${baseApi}/validate-otp`, requestBody)
            .then((res) => {
                setIsLoading(false);

                if (res.status !== 200 || res.data.success === false) {
                    setErrorMessage('wrong_otp');
                    setOtpStatus('wrong');
                    callback(false);

                    return;
                }

                setOtpStatus('correct');
                setActiveScreen(3);
            }).catch((error) => {
                setIsLoading(false);
                setOtpStatus('wrong');
                setErrorMessage('default_error');
                console.error(error);
            });
    };

    return (
        <>
            <Overlay display={isLoading} />
            <StyledLoading display={isLoading} src={Loading} />
            <GlobalStyle />
            <Container>
                <StyledLogo />
                <StyledLinksContainer flexEnd>
                    {activeScreen === 2 && (
                        <StyledButton
                            variant='link'
                            onClick={() => {
                                setOtpStatus('initial');
                                setActiveScreen(1);
                            }}
                        >
                            {translation.secondPage.backButton}
                        </StyledButton>
                    )}
                    <StyledButton
                        variant='link'
                        onClick={() => { setLang(lang === 'ar' ? 'en' : 'ar'); }}
                    >
                        {translation.lang}
                    </StyledButton>
                </StyledLinksContainer>
                <Card className={`termination ${activeScreen === 3 ? 'wider' : ''} ${activeScreen > 3 ? 'final' : ''}`}>
                    {activeScreen === 1 && (
                        <TerminationForm
                            apiResponse={apiResponse}
                            errorMsg={errorMessage}
                            isLoading={isLoading}
                            mobileNumber={mobileNumber}
                            nationalId={nationalId}
                            setMobileNumber={setMobileNumber}
                            setNationalId={setNationalId}
                            translation={translation.terminationPage}
                            onSubmit={handleDetailsSubmission}
                        />
                    )}
                    {activeScreen === 2 && (
                        <OtpVerification
                            OTPMessage={errorMessage}
                            OTPStatus={otpStatus}
                            sendOtp={resendOTP}
                            setActiveScreen={setActiveScreen}
                            setIsLoading={setIsLoading}
                            setOtpStatus={setOtpStatus}
                            translation={translation.terminationPage.otpModal}
                            terminationMode
                            onChange={handleOTPSubmission}
                        />
                    )}
                    {activeScreen === 3 && (
                        <ConfirmTermination
                            apiResponse={apiResponse}
                            content={translation.terminationPage.confirmTermination}
                            locale={lang}
                            mobileNumber={mobileNumber}
                            setActiveScreen={setActiveScreen}
                            setIsLoading={setIsLoading}
                        />
                    )}
                    {activeScreen === 4 && (
                        <TerminationSuccess
                            content={translation.terminationPage.terminationSuccess}
                            mobileNumber={mobileNumber}
                            setActiveScreen={setActiveScreen}
                        />
                    )}
                    {activeScreen === 5 && (
                        <TerminationFailure
                            content={translation.terminationPage.terminationFailure}
                            locale={lang}
                            mobileNumber={mobileNumber}
                            setActiveScreen={setActiveScreen}
                        />
                    )}
                </Card>
            </Container>
        </>
    );
};

export default TerminationRequest;
