import axios from 'axios';
import PropTypes from 'prop-types';
import React, { useState } from 'react';

import PhoneInput from '../phone-input-field';

import {
    MainStyled,
    MobileStyled,
    ButtonStyled,
    CancelLinkStyled,
    DescriptionStyled,
    TitleStyled,
    HelpTextStyled,
    SmsConfirmationTextStyled,
    PhoneInputWrapperStyled,
    TermsAndConditionsStyled,
    TermsAndConditionsTextStyled,
    TermsAndConditionsLinkStyled
} from './styles';

const baseApi = process.env.REACT_APP_API_LC;

const ConfirmTermination = ({
    apiResponse,
    content,
    setActiveScreen,
    mobileNumber,
    locale,
    setIsLoading
}) => {
    const [ altMobileNumber, setAltMobileNumber ] = useState('');

    const handleSubmit = () => {
        const requestId = `${mobileNumber}_${+new Date()}`;

        const requestBody = {
            request_id: requestId,
            caller_id: 'Portal',
            reference_number: requestId,
            uid: apiResponse?.ssoid,
            iccid: apiResponse?.iccid,
            imsi: apiResponse?.imsi,
            msisdn: `966${mobileNumber}`,
            id_type: apiResponse?.cust_id_type,
            iqama_id: apiResponse?.cust_id
        };
        setIsLoading(true);
        axios.post(`${baseApi}/terminate-request`, requestBody)
            .then((res) => {
                setIsLoading(false);

                if (res.status === 200 && res.data.success) {
                    const req = {
                        jawwy_mobile: `966${mobileNumber}`,
                        contact_number: altMobileNumber ? `966${altMobileNumber}` : null,
                        sso_id: apiResponse?.ssoid
                    };
                    axios.post(`${baseApi}/create-case-terminate-request`, req)
                        .then((response) => {
                            if (response.status === 200 && res.data.success)
                                setActiveScreen(4);
                            else
                                setActiveScreen(5);
                        })
                        .catch((error) => {
                            console.error(error?.msg);

                            setActiveScreen(5);
                        });
                } else
                    setActiveScreen(5);
            }).catch((error) => {
                console.error(error?.msg);

                setActiveScreen(5);
                setIsLoading(false);
            });
    };

    return (
        <MainStyled>
            <TitleStyled>{content.title}</TitleStyled>
            <DescriptionStyled>
                {content.description}
            </DescriptionStyled>
            <MobileStyled>
                {mobileNumber}
            </MobileStyled>
            <HelpTextStyled dangerouslySetInnerHTML={{ __html: content.tryAgainInCaseIssues }} />
            <PhoneInputWrapperStyled>
                <PhoneInput
                    fieldMsg={{
                        message: apiResponse?.error
                            ?
                            content.mobileNumber.errors.api[apiResponse?.error]
                            :
                            !content.mobileNumber.message,
                        isError: !!apiResponse?.error
                    }}
                    id='input-mobile-number'
                    label={content.altMobileNumber.label}
                    value={altMobileNumber}
                    isJawwyOnly
                    onChange={(e) => {
                        const value = e.target.value.replace(/[^0-9]/g, '').slice(0, 9);

                        setAltMobileNumber(value);
                    }}
                />
                <SmsConfirmationTextStyled>{content.altMobileNumber.confirmationText}</SmsConfirmationTextStyled>
                <TermsAndConditionsStyled>
                    <TermsAndConditionsTextStyled>{content.termsAndConditions.text}</TermsAndConditionsTextStyled>
                    <TermsAndConditionsLinkStyled
                        href={`https://www.jawwy.sa/${locale}/content/terms-and-conditions`}
                        rel='noreferrer'
                        target='_blank'
                    >
                        {content.termsAndConditions.link}
                    </TermsAndConditionsLinkStyled>
                </TermsAndConditionsStyled>
            </PhoneInputWrapperStyled>
            <ButtonStyled type='button' onClick={handleSubmit}>
                {content.submit}
            </ButtonStyled>
            <CancelLinkStyled href='#' onClick={() => setActiveScreen(1)}>{content.cancel}</CancelLinkStyled>
        </MainStyled>
    );
};

export default ConfirmTermination;

ConfirmTermination.propTypes = {
    apiResponse: PropTypes.shape().isRequired,
    content: PropTypes.shape().isRequired,
    locale: PropTypes.string.isRequired,
    mobileNumber: PropTypes.string.isRequired,
    setActiveScreen: PropTypes.func.isRequired,
    setIsLoading: PropTypes.func.isRequired
};
