import PropTypes from 'prop-types';
import React from 'react';

import {
    InputBlock,
    InputMask,
    PhoneTitle,
    PhoneWrap,
    InputWarning,
    StyledTextInput
} from './styles';

const PhoneInput = ({
    label, value, onChange, fieldMsg, isJawwyOnly, required, ...props
}) => (
    <PhoneWrap>
        <PhoneTitle>
            {required && <span className='required'>* </span>}
            {label && label.toUpperCase()}
        </PhoneTitle>
        <InputBlock>
            <InputMask>+966</InputMask>
            <StyledTextInput
                focused={value && !fieldMsg?.isError}
                placeholder={isJawwyOnly ? '530 000 000' : '000000000'}
                value={value || ''}
                preserveErrorSpace
                onChange={(e) => {
                    e.target.value = e.target.value.slice(0, 9);
                    onChange(e);
                }}
                {...props}
            />
        </InputBlock>
        {fieldMsg?.isError}
        <InputWarning isError={fieldMsg?.isError}>{fieldMsg?.message}</InputWarning>
    </PhoneWrap>
);

PhoneInput.propTypes = {
    fieldMsg: PropTypes.shape({
        isError: PropTypes.bool,
        message: PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.bool
        ])
    }),
    isJawwyOnly: PropTypes.bool,
    label: PropTypes.string,
    onChange: PropTypes.func,
    required: PropTypes.bool,
    value: PropTypes.string
};

PhoneInput.defaultProps = {
    fieldMsg: null,
    isJawwyOnly: false,
    label: null,
    onChange: null,
    required: false,
    value: null
};

export default PhoneInput;
