export const getUrlParameter = (name) => {
    // eslint-disable-next-line no-param-reassign
    name = name.replace(/[[]/, '\\[').replace(/[\]]/, '\\]');
    const regex = new RegExp(`[\\?&]${ name }=([^&#]*)`);
    const results = regex.exec(window.location.search.replace(/&amp;/g, '&'));

    return results === null ? null : decodeURIComponent(results[1].replace(/\+/g, ' '));
};

export const validateMobileNumber = (mobileNumber) => {
    const phoneValidator = /^[0-9]{9}$/;

    if (!mobileNumber.match(phoneValidator))
        return false;

    return true;

    // Old validation
    /* const phoneValidator = /^(009665|9665|\+9665|05|5)(5|0|3|6|4|9|1|8|7)([0-9]{7})$/
    if (!mobileNumber.match(phoneValidator)) {
        return false
    } else {
        return true
    } */
};

export const validateNationalId = (nationalId) => {
    const nationalIdValidator = /^[0-9]{10}$/;

    if (!nationalId.match(nationalIdValidator))
        return false;

    return true;
};

export const isJson = (text) => {
    if (/^[\],:{}\s]*$/.test(text.replace(/\\["\\/bfnrtu]/g, '@')
        .replace(/"[^"\\\n\r]*"|true|false|null|-?\d+(?:\.\d*)?(?:[eE][+-]?\d+)?/g, ']')
        .replace(/(?:^|:|,)(?:\s*\[)+/g, '')))
        return true;

    return false;
};

// eslint-disable-next-line no-useless-escape
export const isSafari = !!navigator.userAgent.match(/Version\/[\d\.]+.*Safari/);
