import PropTypes from 'prop-types';
import React from 'react';

import IconAbsheerLinked from '../../assets/icons/icon-absheer-linked.svg';
import IconLink from '../../assets/icons/icon-link.svg';
import IconTick from '../../assets/icons/icon-tick.svg';

import {
    MainStyled,
    MobileStyled,
    ButtonStyled,
    DescriptionStyled
} from './styles';

const FingerprintUpdateSuccess = ({ content, setActiveScreen, mobileNumber }) => {
    const handleClick = () => {
        setActiveScreen(1);
    };

    return (
        <MainStyled>
            <DescriptionStyled>
                {content.description}
            </DescriptionStyled>
            <MobileStyled>
                {mobileNumber}
            </MobileStyled>
            <IconLink className='icon-link' />
            <IconAbsheerLinked />
            <IconTick />
            <ButtonStyled type='button' onClick={handleClick}>
                {content.submit}
            </ButtonStyled>
        </MainStyled>
    );
};

export default FingerprintUpdateSuccess;

FingerprintUpdateSuccess.propTypes = {
    content: PropTypes.shape().isRequired,
    mobileNumber: PropTypes.string.isRequired,
    setActiveScreen: PropTypes.func.isRequired
};
