import PropTypes from 'prop-types';
import React from 'react';

import IconAbsheerNotLinked from '../../assets/icons/icon-absheer-not-linked.svg';
import IconX from '../../assets/icons/icon-x.svg';

import {
    InCaseIssuesStyled,
    MainStyled,
    ButtonStyled,
    HelpTextStyled
} from './styles';

const domain = process.env.REACT_APP_DOMAIN;

const FingerprintUpdateFailure = ({
    content,
    setActiveScreen
}) => {
    const handleClick = () => {
        setActiveScreen(1);
    };

    return (
        <MainStyled>
            <HelpTextStyled dangerouslySetInnerHTML={{ __html: content.helpText.replace('{domain}', domain) }} />
            <IconAbsheerNotLinked />
            <IconX />
            <ButtonStyled type='button' onClick={handleClick}>
                {content.submit}
            </ButtonStyled>
            <InCaseIssuesStyled dangerouslySetInnerHTML={{ __html: content.tryAgainInCaseIssues.replace('{domain}', domain) }} />
        </MainStyled>
    );
};

export default FingerprintUpdateFailure;

FingerprintUpdateFailure.propTypes = {
    content: PropTypes.shape().isRequired,
    setActiveScreen: PropTypes.func.isRequired
};
