import { IconCheckOutline } from '@prototypeinteractive/jawwy-ui-kit-react';
import PropTypes from 'prop-types';
import React from 'react';

import {
    MainStyled,
    MobileCrossed,
    ButtonStyled,
    DescriptionStyled,
    TitleStyled
} from './styles';

const TerminationSuccess = ({ content, setActiveScreen, mobileNumber }) => {
    const handleClick = () => {
        setActiveScreen(1);
    };

    return (
        <MainStyled>
            <TitleStyled>{content.title}</TitleStyled>
            <DescriptionStyled>
                {content.description}
            </DescriptionStyled>
            <MobileCrossed>
                {mobileNumber}
            </MobileCrossed>
            <IconCheckOutline />
            <ButtonStyled type='button' onClick={handleClick}>
                {content.submit}
            </ButtonStyled>
        </MainStyled>
    );
};

export default TerminationSuccess;

TerminationSuccess.propTypes = {
    content: PropTypes.shape().isRequired,
    mobileNumber: PropTypes.string.isRequired,
    setActiveScreen: PropTypes.func.isRequired
};
