/* eslint-disable no-console */
import axios from 'axios';
import React, { useEffect, useState } from 'react';

import BlockingOptions from '../../components/jawwal-control/blocking-options';
import BlockingStatus from '../../components/jawwal-control/blocking-status';
import OtpVerification from '../../components/jawwal-control/otp-verification';
import UserDetails from '../../components/jawwal-control/user-details';
import { getUrlParameter /* , isJson, isSafari */ } from '../../config/helpers';
import { Screens } from '../../config/jawwal-control/enums';
import '@prototypeinteractive/jawwy-ui-kit-react/lib/index.css';
import arTranslation from '../../data/jawwal-control/ar.json';
import enTranslation from '../../data/jawwal-control/en.json';
import Loading from '../../loading.png';

import {
    Container, GlobalStyle, Card, StyledLogo, StyledLinksContainer, StyledButton, Overlay, StyledLoading
} from './styles';

const baseApi = process.env.REACT_APP_API_JC;
const pathPrefix = process.env.PUBLIC_URL || '/jawwal-control';

const JawwalControl = () => {
    const [ activeScreen, setActiveScreen ] = useState(Screens.UserDetails);
    const [ nationalId, setNationalId ] = useState('');
    const [ mobileNumber, setMobileNumber ] = useState('');
    const [ otpStatus, setOtpStatus ] = useState('initial');
    const [ errorMessage, setErrorMessage ] = useState('');
    const [ isLoading, setIsLoading ] = useState(false);
    const [ transactionId, setTransactionId ] = useState('');
    const [ lang, setLang ] = useState('ar');
    const [ translation, setTranslation ] = useState(enTranslation);
    const [ firstLoad, setFirstLoad ] = useState(true);
    const [ apiResponse, setApiResponse ] = useState({});
    const [ requestExists, setRequestExists ] = useState();
    const [ service, setService ] = useState();
    const [ serviceStatus, setServiceStatus ] = useState();
    const [ newService, setNewService ] = useState(null);
    const [ newServiceStatus, setNewServiceStatus ] = useState(null);
    const [ isAlreadySubscribed, setIsAlreadySubscribed ] = useState(false);

    useEffect(() => {
        const isAr = lang !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        const stateObj = { id: '101' };

        if (!firstLoad) {
            let queryParam = window.location.search;

            if (isAr) {
                queryParam = queryParam.replace('lang=en&', '');
                queryParam = queryParam.replace('lang=en', '');
                window.history.replaceState(stateObj,
                    'Jawwal Control', queryParam === '?' ? pathPrefix : `${pathPrefix}${queryParam}`);
            } else {
                queryParam = queryParam.replace('?', '&');

                if (!queryParam.includes('lang=en')) {
                    window.history.replaceState(stateObj,
                        'Jawwal Control', `${pathPrefix}?lang=en${queryParam}`);
                }
            }
        }
    }, [ lang, firstLoad ]);

    useEffect(() => {
        const isAr = getUrlParameter('lang') !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        setFirstLoad(false);
    }, []);

    useEffect(() => {
        setApiResponse({});
    }, [ nationalId, mobileNumber ]);

    const handleUserDetailsSubmission = () => {
        const requestBody = {
            msisdn: `966${mobileNumber}`,
            preferred_lng: lang,
            iqama_id_c: nationalId
        };

        setIsLoading(true);
        setErrorMessage('');

        axios.post(`${baseApi}/validate-msisdn`, requestBody).then((res) => {
            setIsLoading(false);

            if (res.data.status === 'success') {
                setTransactionId(res.data.transactionid);
                setActiveScreen(Screens.OtpVerificationUserDetails);
            } else if (![ 'not_found', 'Invalid_ID_Number' ].includes(res.data.error_code))
                throw new Error();

            if ([ 'not_found', 'Invalid_ID_Number' ].includes(res.data.error_code)) {
                setErrorMessage('not_found');
                setIsLoading(false);
            } else
                setApiResponse(res.data);
        }).catch((error) => {
            setErrorMessage('default_error');
            setIsLoading(false);
            console.error(error);
        });
    };

    const handleBlockingOptionsSubmission = () => {
        const requestBody = {
            msisdn: `966${mobileNumber}`,
            preferred_lng: lang,
            iqama_id_c: nationalId
        };

        setIsLoading(true);
        setErrorMessage('');

        axios.post(`${baseApi}/validate-msisdn`, requestBody).then((res) => {
            setIsLoading(false);

            if (res.data.status === 'success') {
                setTransactionId(res.data.transactionid);
                setActiveScreen(Screens.OtpVerificationBlockingOptions);
            } else if (![ 'not_found', 'Invalid_ID_Number' ].includes(res.data.error_code))
                throw new Error();

            if ([ 'not_found', 'Invalid_ID_Number' ].includes(res.data.error_code)) {
                setErrorMessage('not_found');
                setIsLoading(false);
            } else
                setApiResponse(res.data);
        }).catch((error) => {
            setErrorMessage('default_error');
            setIsLoading(false);
            console.error(error);
        });
    };

    const resendOTP = () => {
        const requestBody = {
            caller_id: 'Portal',
            transaction_id: transactionId
        };
        setIsLoading(true);
        axios.post(`${baseApi}/resend-otp`, requestBody).then((res) => {
            setIsLoading(false);

            if (res.data.status === 'success')
                setOtpStatus('initial');
            else
                throw new Error();
        }).catch((error) => {
            setIsLoading(false);
            setErrorMessage('default_error');
            console.error(error);
        });
    };

    const handleOTPSubmission = async (screen, value, callback) => {
        if (value.length < 4)
            return;

        let response;

        setIsLoading(true);

        try {
            response = await axios.post(`${baseApi}/validate-otp`, {
                caller_id: 'Portal',
                otp: value,
                transaction_id: transactionId
            });
        } catch (error) {
            setIsLoading(false);
            setOtpStatus('wrong');
            setErrorMessage('default_error');
            console.error(error);

            return;
        }

        setIsLoading(false);

        if (response.status !== 200 || response.data.success !== true) {
            setErrorMessage('wrong_otp');
            setOtpStatus('wrong');
            callback(false);

            return;
        }

        setErrorMessage();
        setOtpStatus('correct');

        switch (screen) {
            case Screens.UserDetails:
                try {
                    response = await axios.post(`${baseApi}/get-aljawal-lite`, {
                        body: {
                            serviceIdentifier: mobileNumber
                        }
                    });
                } catch (error) {
                    console.log('get-aljawal-lite error', error);
                }

                if (response.data.status?.result === 'ERROR') {
                    if (response.data.status?.error?.errorCode === 50005) {
                        if (response.data.status?.error?.errorDescription === 'subscriber not found')
                            setIsAlreadySubscribed(true);
                    } else {
                        setOtpStatus('wrong');
                        setErrorMessage(response.data.status?.error?.errorDescription);
                        callback(false);

                        return;
                    }
                }

                setServiceStatus(response.data?.body?.serviceStatus);
                setService(response.data?.body?.service);
                setActiveScreen(Screens.BlockingOptions);

                setOtpStatus('initial');
                setErrorMessage();

                break;

            case Screens.BlockingOptions:
                // Note: According to the discussion in the call with Jawwy, here we need
                //       to make 2 calls to manageAljawalLite in order to:
                //       1. First undo the current settings
                //       2. Apply the new settings

                // Note: Jawwy's reasoning is that ESB can't store any state on their side
                //       so we have to cancel current settings first :)

                try {
                    // Operation: Undo current settings

                    response = await axios.post(`${baseApi}/manage-aljawal-lite`, {
                        body: {
                            serviceIdentifier: mobileNumber,
                            serviceType: 'inbound',
                            service: newService,
                            serviceStatus: newServiceStatus,
                            isAlreadySubscribed
                        }
                    });
                } catch (error) {
                    console.log('manage-aljawal-lite error', error);
                }

                try {
                    // Operation: Apply new settings

                    response = await axios.post(`${baseApi}/manage-aljawal-lite`, {
                        body: {
                            serviceIdentifier: mobileNumber,
                            serviceType: 'inbound',
                            service: newService,
                            serviceStatus: newServiceStatus,
                            isAlreadySubscribed
                        }
                    });
                } catch (error) {
                    console.log('manage-aljawal-lite error', error);
                }

                setActiveScreen(Screens.BlockingStatus);

                // This status will depend on the actual response, here we mock it for internal QA
                setRequestExists(response.data.status?.result !== 'SUCCESS');

                break;

            default:
        }
    };

    const handleReturnLink = () => {
        setErrorMessage();
        setOtpStatus('initial');
        setActiveScreen(Screens.UserDetails);
    };

    return (
        <>
            <Overlay display={isLoading} />
            <StyledLoading display={isLoading} src={Loading} />
            <GlobalStyle />
            <Container>
                <StyledLogo />
                <StyledLinksContainer flexEnd>
                    {false && activeScreen !== Screens.UserDetails && (
                        <StyledButton
                            variant='link'
                            onClick={() => {
                                setOtpStatus('initial');
                                setActiveScreen(Screens.UserDetails);
                            }}
                        >
                            {translation.backButton}
                        </StyledButton>
                    )}
                    <StyledButton
                        variant='link'
                        onClick={() => { setLang(lang === 'ar' ? 'en' : 'ar'); }}
                    >
                        {translation.lang}
                    </StyledButton>
                </StyledLinksContainer>
                <Card>
                    {activeScreen === Screens.UserDetails && (
                        <UserDetails
                            apiResponse={apiResponse}
                            errorMsg={errorMessage}
                            isLoading={isLoading}
                            mobileNumber={mobileNumber}
                            nationalId={nationalId}
                            setMobileNumber={setMobileNumber}
                            setNationalId={setNationalId}
                            translation={translation}
                            onSubmit={handleUserDetailsSubmission}
                        />
                    )}
                    {activeScreen === Screens.OtpVerificationUserDetails && (
                        <OtpVerification
                            handleReturnLink={handleReturnLink}
                            mobileNumber={mobileNumber}
                            OTPMessage={errorMessage}
                            OTPStatus={otpStatus}
                            sendOtp={resendOTP}
                            setOtpStatus={setOtpStatus}
                            translation={translation}
                            onChange={(value, callback) => handleOTPSubmission(Screens.UserDetails, value, callback)}
                        />
                    )}
                    {activeScreen === Screens.BlockingOptions && (
                        <BlockingOptions
                            isLoading={isLoading}
                            service={service}
                            serviceStatus={serviceStatus}
                            setNewService={setNewService}
                            setNewServiceStatus={setNewServiceStatus}
                            translation={translation}
                            onSubmit={handleBlockingOptionsSubmission}
                        />
                    )}
                    {activeScreen === Screens.OtpVerificationBlockingOptions && (
                        <OtpVerification
                            mobileNumber={mobileNumber}
                            OTPMessage={errorMessage}
                            OTPStatus={otpStatus}
                            sendOtp={resendOTP}
                            setActiveScreen={setActiveScreen}
                            setOtpStatus={setOtpStatus}
                            translation={translation}
                            onChange={(value, callback) => handleOTPSubmission(Screens.BlockingOptions, value, callback)}
                        />
                    )}
                    {activeScreen === Screens.BlockingStatus && (
                        <BlockingStatus
                            requestExists={requestExists}
                            translation={translation}
                        />
                    )}
                </Card>
            </Container>
        </>
    );
};

export default JawwalControl;
