import PropTypes from 'prop-types';
import React from 'react';
import { Helmet } from 'react-helmet';

import IconClock from '../../../assets/icons/icon-clock.svg';
import IconTick from '../../../assets/icons/icon-tick.svg';

import {
    TitleContainer, Title, Main, Icon, Description
} from './styles';

const BlockingStatus = ({
    requestExists,
    translation
}) => (
    <Main>
        <Helmet>
            <title>
                {translation.blockingStatus.title}
            </title>
        </Helmet>
        <TitleContainer>
            <Title>
                {translation.blockingStatus.title}
            </Title>
        </TitleContainer>
        <Icon>
            {requestExists ? <IconClock /> : <IconTick />}
        </Icon>
        <Description>
            {requestExists ?
                translation.blockingStatus.requestsAlreadyExists :
                translation.blockingStatus.newRequestReceived}
            <br />
            <br />
            {translation.blockingStatus.confirmationViaSmsSoon}
        </Description>
    </Main>
);

BlockingStatus.propTypes = {
    requestExists: PropTypes.bool,
    translation: PropTypes.shape({
        // eslint-disable-next-line react/forbid-prop-types
        blockingStatus: PropTypes.object
    })
};

BlockingStatus.defaultProps = {
    requestExists: false,
    translation: null
};
export default BlockingStatus;
