import axios from 'axios';

export const createSessionUpdateFingerprint = async (body) => axios.post(`${process.env.REACT_APP_API_SA}/create-session-fingerprint-update`, body);

export const redirectToAbsharPage = (apiKey, session_id, lang) => window.open(`${process.env.REACT_APP_ABSHER_URL}?sessionId=${session_id}&apikey=${apiKey}&systemId=sphr&lang=${lang}`, '_self');

export const getCachedData = async (body) => axios.post(`${process.env.REACT_APP_API_SA}/get-sim-details`, body);

export const iamTokenUpdate = async (body) => axios.post(`${process.env.REACT_APP_API_SA}/update-iam-token-fp`, body);
