import { IconCheck2 } from '@prototypeinteractive/jawwy-ui-kit-react';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';

import IconMobilePhone from '../../assets/icons/icon-mobile-phone.svg';
import { validateNationalId, validateMobileNumber } from '../../config/helpers';
import PhoneInput from '../phone-input-field';

import {
    Description,
    InCaseIssuesStyled,
    TitleContainer,
    Title,
    Main,
    StyledTextInput,
    ButtonStyled,
    RequiredStyled,
    ErrorMsgStyled,
    PhoneInputWrapperStyled,
    SmsConfirmationTextStyled
} from './styles';

const TerminationForm = ({
    altMobileNumber,
    apiResponse,
    translation,
    mobileNumber,
    setMobileNumber,
    nationalId,
    setNationalId,
    onSubmit,
    errorMsg,
    isLoading
}) => {
    const [ isValidMobileNumber, setIsValidMobileNumber ] = useState(true);
    const [ isValidAltMobileNumber, setIsValidAltMobileNumber ] = useState(true);
    const [ isValidNationalId, setIsValidNationalId ] = useState(true);
    const [ isButtonActive, setIsButtonActive ] = useState(false);

    useEffect(() => {
        const handler = (e) => {
            // prevent: "e", "=", ",", "-", "."
            if ([ 69, 187, 188, 189, 190 ].includes(e.keyCode))
                e.preventDefault();
        };

        document.getElementById('input-national-id')?.addEventListener('keydown', handler);
        document.getElementById('input-mobile-number')?.addEventListener('keydown', handler);
    }, []);

    useEffect(() => {
        if (mobileNumber)
            setIsValidMobileNumber(validateMobileNumber(mobileNumber));

        if (altMobileNumber)
            setIsValidAltMobileNumber(validateMobileNumber(altMobileNumber));

        if (nationalId)
            setIsValidNationalId(validateNationalId(nationalId));

        // && altMobileNumber && isValidAltMobileNumber
        if (nationalId && mobileNumber && isValidMobileNumber && isValidNationalId)
            setIsButtonActive(true);
        else
            setIsButtonActive(false);
    }, [
        isValidMobileNumber,
        isValidAltMobileNumber,
        isValidNationalId,
        mobileNumber,
        altMobileNumber,
        nationalId,
        apiResponse ]);

    return (
        <Main>
            <Helmet>
                <title>
                    { translation.title }
                </title>
            </Helmet>
            <TitleContainer>
                <IconMobilePhone height={27} width={14} />
                <Title>
                    { translation.title }
                </Title>
            </TitleContainer>
            <Description>
                { translation.description }
            </Description>
            <StyledTextInput
                appendContent={isValidNationalId && nationalId && !apiResponse?.error && <IconCheck2 height={25} width={22} />}
                error={apiResponse?.error ? translation.nationalId.errors.api[apiResponse?.error] : undefined}
                focused={isValidNationalId && nationalId}
                id='input-national-id'
                label={translation.nationalId.label}
                placeholder='0000000000'
                value={nationalId}
                preserveErrorSpace
                required
                onChange={(e) => {
                    const value = e.target.value.replace(/[^0-9]/g, '').slice(0, 10);

                    setNationalId(value);
                }}
            />
            <PhoneInputWrapperStyled>
                <PhoneInput
                    appendContent={isValidMobileNumber && mobileNumber && !apiResponse?.error && <IconCheck2 height={25} width={22} />}
                    fieldMsg={{
                        message: apiResponse?.error
                            ?
                            translation.mobileNumber.errors.api[apiResponse?.error]
                            :
                            !translation.mobileNumber.message,
                        isError: !!apiResponse?.error
                    }}
                    id='input-mobile-number'
                    label={translation.mobileNumber.label}
                    value={mobileNumber}
                    isJawwyOnly
                    required
                    onChange={(e) => {
                        const value = e.target.value.replace(/[^0-9]/g, '').slice(0, 9);

                        setMobileNumber(value);
                    }}
                />
                <SmsConfirmationTextStyled>{translation.mobileNumber.message}</SmsConfirmationTextStyled>
            </PhoneInputWrapperStyled>
            <ErrorMsgStyled display={errorMsg}>{translation.errors[errorMsg]}</ErrorMsgStyled>
            <ButtonStyled
                disabled={!isButtonActive || isLoading}
                onClick={onSubmit}
            >
                {translation.buttonLabel}
            </ButtonStyled>
            <RequiredStyled>
                {translation.requiredField}
            </RequiredStyled>
            <InCaseIssuesStyled dangerouslySetInnerHTML={{ __html: translation.inCaseIssues }} />
        </Main>
    );
};

TerminationForm.propTypes = {
    altMobileNumber: PropTypes.string,
    apiResponse: PropTypes.shape({
        error: PropTypes.string
    }),
    errorMsg: PropTypes.string,
    isLoading: PropTypes.bool,
    mobileNumber: PropTypes.string,
    nationalId: PropTypes.string,
    onSubmit: PropTypes.func,
    setMobileNumber: PropTypes.func,
    setNationalId: PropTypes.func,
    translation: PropTypes.shape()
};

TerminationForm.defaultProps = {
    altMobileNumber: null,
    apiResponse: null,
    errorMsg: null,
    isLoading: null,
    mobileNumber: null,
    nationalId: null,
    onSubmit: null,
    setMobileNumber: null,
    setNationalId: null,
    translation: null
};
export default TerminationForm;
