import PropTypes from 'prop-types';
import React from 'react';

import IconWarning from '../../assets/icons/icon-warning.svg';

import {
    MainStyled,
    MobileStyled,
    ButtonStyled,
    HelpTextStyled,
    TitleStyled
} from './styles';

const TerminationFailure = ({
    content,
    setActiveScreen,
    mobileNumber
}) => {
    const handleClick = () => {
        setActiveScreen(1);
    };

    return (
        <MainStyled>
            <TitleStyled>{content.title}</TitleStyled>
            <HelpTextStyled dangerouslySetInnerHTML={{ __html: content.tryAgainInCaseIssues }} />
            <MobileStyled>
                {mobileNumber}
            </MobileStyled>
            <IconWarning />
            <ButtonStyled type='button' onClick={handleClick}>
                {content.submit}
            </ButtonStyled>
        </MainStyled>
    );
};

export default TerminationFailure;

TerminationFailure.propTypes = {
    content: PropTypes.shape().isRequired,
    mobileNumber: PropTypes.string.isRequired,
    setActiveScreen: PropTypes.func.isRequired
};
