import { IconCheckOutline, IconCircle, IconCloseOutline } from '@prototypeinteractive/jawwy-ui-kit-react';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Helmet } from 'react-helmet';

import {
    InCaseIssuesStyled,
    ErrorMsg,
    Main,
    ResendCode,
    ResendCodePlaceholder,
    ReturnLink,
    StyledOTPInput,
    SubErrorMsg,
    DescriptionStyled,
    MobileNumberStyled,
    ResendCodeStyled,
    TitleStyled
} from './styles';

const OtpVerification = ({
    translation,
    mobileNumber,
    onChange,
    OTPStatus,
    OTPMessage,
    sendOtp,
    setOtpStatus,
    handleReturnLink
}) => {
    const [ showResendOTP, setShowResendOTP ] = useState(true);
    const [ otpResended, setOtpResended ] = useState(false);
    const [ otpInputKey, setOtpInputKey ] = useState(false);

    const translationFormatted = translation.otpVerification ?? translation;

    return (
        <Main>
            <Helmet>
                <title>
                    {translation.otpVerification.title}
                </title>
            </Helmet>
            <TitleStyled>
                ****
            </TitleStyled>
            <DescriptionStyled>{translationFormatted.description}</DescriptionStyled>
            <MobileNumberStyled>{mobileNumber}</MobileNumberStyled>
            <ReturnLink onClick={handleReturnLink}>
                {translationFormatted.wrongNumberLink}
            </ReturnLink>
            <StyledOTPInput disabled={[ 'wrong', 'correct' ].includes(OTPStatus)} key={otpInputKey} numDigits={4} onChange={onChange} />
            {!true && showResendOTP && (![ 'wrong', 'correct' ].includes(OTPStatus)) ? (
                <ResendCode onClick={() => { setShowResendOTP(false); sendOtp(); }}>
                    {translationFormatted.resendCode}
                </ResendCode>
            ) : (
                <ResendCodePlaceholder>&nbsp;</ResendCodePlaceholder>
            )}
            {OTPStatus !== 'initial' && (
                <ErrorMsg display={OTPMessage}>
                    {translationFormatted.errors[OTPMessage]?.msg}
                    <SubErrorMsg>{translationFormatted.errors[OTPMessage]?.sub_msg}</SubErrorMsg>
                </ErrorMsg>
            )}
            {OTPStatus === 'correct' && <IconCheckOutline height={100} width={100} />}
            {OTPStatus === 'initial' && <IconCircle height={100} width={100} />}
            {OTPStatus === 'wrong' && <IconCloseOutline height={100} width={100} />}
            <ResendCodeStyled>
                <span className='question'>{translationFormatted.resendQuestion}</span>
                <button
                    className='resend'
                    type='button'
                    onClick={() => {
                        setShowResendOTP(false);
                        setOtpResended(true);
                        setOtpStatus('initial');
                        setOtpInputKey(!otpInputKey);
                        sendOtp();
                    }}
                >
                    {translationFormatted.resend}
                </button>
            </ResendCodeStyled>
            {otpResended && <InCaseIssuesStyled dangerouslySetInnerHTML={{ __html: translationFormatted.tryAgainInCaseIssues }} /> }
        </Main>
    );
};

OtpVerification.propTypes = {
    handleReturnLink: PropTypes.func,
    mobileNumber: PropTypes.string,
    onChange: PropTypes.func,
    OTPMessage: PropTypes.string,
    OTPStatus: PropTypes.string,
    sendOtp: PropTypes.func,
    setOtpStatus: PropTypes.func,
    translation: PropTypes.shape({
        // eslint-disable-next-line react/forbid-prop-types
        otpVerification: PropTypes.object
    })
};

OtpVerification.defaultProps = {
    handleReturnLink: null,
    mobileNumber: null,
    onChange: null,
    OTPMessage: null,
    OTPStatus: null,
    sendOtp: null,
    setOtpStatus: null,
    translation: null
};

export default OtpVerification;
