import React, { useEffect, useState } from 'react';

import ConfirmTermination from '../../components/confirm-termination';
import FingerprintUpdateFailure from '../../components/fingerprint-update-failure';
import FingerprintUpdateForm from '../../components/fingerprint-update-form';
import FingerprintUpdateSuccess from '../../components/fingerprint-update-success';
import { getUrlParameter } from '../../config/helpers';
import '@prototypeinteractive/jawwy-ui-kit-react/lib/index.css';
import arTranslation from '../../data/ar.json';
import enTranslation from '../../data/en.json';
import Loading from '../../loading.png';
import {
    createSessionUpdateFingerprint, redirectToAbsharPage,
    getCachedData, iamTokenUpdate
} from '../../services';

import {
    Container, GlobalStyle, Card, StyledLogo, StyledLinksContainer, StyledButton, Overlay, StyledLoading
} from './styles';

const apiKey = process.env.REACT_APP_API_KEY;
const pathPrefix = process.env.PUBLIC_URL || '/customer-fingerprint-update';

const CustomerFingerprintUpdate = () => {
    const [ activeScreen, setActiveScreen ] = useState(1);
    const [ nationalId, setNationalId ] = useState('');
    const [ mobileNumber, setMobileNumber ] = useState('');
    const [ errorMessage, setErrorMessage ] = useState('');
    const [ isLoading, setIsLoading ] = useState(false);
    // const [ transactionId, setTransactionId ] = useState('');
    const [ lang, setLang ] = useState('ar');
    const [ translation, setTranslation ] = useState(arTranslation);
    const [ firstLoad, setFirstLoad ] = useState(true);
    const [ apiResponse, setApiResponse ] = useState({});

    useEffect(() => {
        const isAr = lang !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        const stateObj = { id: '101' };

        if (!firstLoad) {
            let queryParam = window.location.search;

            if (isAr) {
                queryParam = queryParam.replace('lang=en&', '');
                queryParam = queryParam.replace('lang=en', '');
                window.history.replaceState(stateObj,
                    'Download Document', queryParam === '?' ? pathPrefix : `${pathPrefix}${queryParam}`);
            } else {
                queryParam = queryParam.replace('?', '&');

                if (!queryParam.includes('lang=en')) {
                    window.history.replaceState(stateObj,
                        'Download Document', `${pathPrefix}?lang=en${queryParam}`);
                }
            }
        }
    }, [ lang, firstLoad ]);

    async function updateIamToken(resp, iamToken) {
        try {
            const iamTokenBody = {
                iccid: resp.data.iccid,
                nationality: resp.data.nationality,
                msisdn: resp.data.msisdn,
                session_id: resp.data.session_id,
                request_id: Date.now().toString(),
                referenceNumber: resp.data.reference_number,
                customerIdType: resp.data.id_type,
                customerIdNumber: resp.data.id_number,
                imsi: resp.data.imsi,
                iamToken
            };
            const tokenResp = await iamTokenUpdate(iamTokenBody);
            const { success: tokenSuccess, status_code } = tokenResp.data;

            if (status_code || tokenSuccess === false)
                setErrorMessage(status_code);

            return tokenSuccess;
        } catch (e) {
            setErrorMessage(e.response?.status ?? 'Error happened');

            return false;
        }
    }

    useEffect(() => {
        const isAr = getUrlParameter('lang') !== 'en';
        document.getElementsByTagName('html')[0].setAttribute('dir', isAr ? 'rtl' : 'ltr');
        setTranslation(isAr ? arTranslation : enTranslation);
        setLang(isAr ? 'ar' : 'en');
        setFirstLoad(false);

        const errorCode = getUrlParameter('errorCode');
        const session = getUrlParameter('sessionId');
        const iamToken = getUrlParameter('iamToken');
        const langExists = getUrlParameter('lang') !== null;

        (async function asyncCalls() {
            if (session && iamToken) {
                setIsLoading(true);
                const simData = await getCachedData({ session_id: session });
                const { success, preferred_lng } = simData.data;

                if (!langExists) {
                    window.location = `${window.location.href}&lang=${preferred_lng ?? 'en'}`;

                    return;
                }

                setIsLoading(false);

                if (success) {
                    if (!errorCode && iamToken) {
                        setIsLoading(true);
                        const resp = await updateIamToken(simData, iamToken);
                        setIsLoading(false);

                        if (resp)
                            setActiveScreen(4);
                        else
                            setActiveScreen(5);
                    } else if (errorCode) {
                        setErrorMessage(true);
                        setActiveScreen(5);
                        setIsLoading(false);
                    }
                } else {
                    setActiveScreen(5);
                    setIsLoading(false);
                }
            }
        }());
    }, []);

    useEffect(() => {
        setApiResponse({});
    }, [ nationalId, mobileNumber ]);

    const handleDetailsSubmission = () => {
        const requestBody = {
            msisdn: `966${mobileNumber}`,
            id_number: nationalId,
            preferred_lng: lang,
            callback_url: `${window.location.origin}${window.location.pathname}`
        };
        setIsLoading(true);
        setErrorMessage('');
        createSessionUpdateFingerprint(requestBody).then((res) => {
            setIsLoading(false);
            const { session_id, success, error_code } = res.data;

            if (success) {
                redirectToAbsharPage(apiKey, session_id, lang);

                // setTransactionId(res.data.transactionid);
                setIsLoading(true);
            } else if (![ 'not_found', 'Invalid_ID_Number' ].includes(error_code))
                throw new Error();

            if ([ 'not_found', 'Invalid_ID_Number' ].includes(error_code)) {
                setErrorMessage('not_found');
                setIsLoading(false);
            } else
                setApiResponse(res.data);
        }).catch((error) => {
            setErrorMessage('default_error');
            setIsLoading(false);
            console.error(error);
        });
    };

    return (
        <>
            <Overlay display={isLoading} />
            <StyledLoading display={isLoading} src={Loading} />
            <GlobalStyle />
            <Container>
                <StyledLogo />
                <StyledLinksContainer flexEnd>
                    {activeScreen === 2 && (
                        <StyledButton
                            variant='link'
                            onClick={() => {
                                setActiveScreen(1);
                            }}
                        >
                            {translation.secondPage.backButton}
                        </StyledButton>
                    )}
                    <StyledButton
                        variant='link'
                        onClick={() => { setLang(lang === 'ar' ? 'en' : 'ar'); }}
                    >
                        {translation.lang}
                    </StyledButton>
                </StyledLinksContainer>
                <Card className={`termination ${activeScreen === 3 ? 'wider' : ''} ${activeScreen > 3 ? 'final' : ''}`}>
                    {activeScreen === 1 && (
                        <FingerprintUpdateForm
                            apiResponse={apiResponse}
                            errorMsg={errorMessage}
                            isLoading={isLoading}
                            mobileNumber={mobileNumber}
                            nationalId={nationalId}
                            setMobileNumber={setMobileNumber}
                            setNationalId={setNationalId}
                            translation={translation.customerFingerprintUpdatePage}
                            onSubmit={handleDetailsSubmission}
                        />
                    )}
                    {activeScreen === 3 && (
                        <ConfirmTermination
                            apiResponse={apiResponse}
                            content={translation.customerFingerprintUpdatePage.confirmTermination}
                            locale={lang}
                            mobileNumber={mobileNumber}
                            setActiveScreen={setActiveScreen}
                            setIsLoading={setIsLoading}
                        />
                    )}
                    {activeScreen === 4 && (
                        <FingerprintUpdateSuccess
                            content={translation.customerFingerprintUpdatePage.terminationSuccess}
                            mobileNumber={mobileNumber}
                            setActiveScreen={setActiveScreen}
                        />
                    )}
                    {activeScreen === 5 && (
                        <FingerprintUpdateFailure
                            content={translation.customerFingerprintUpdatePage.terminationFailure}
                            locale={lang}
                            mobileNumber={mobileNumber}
                            setActiveScreen={setActiveScreen}
                        />
                    )}
                </Card>
            </Container>
        </>
    );
};

export default CustomerFingerprintUpdate;
