import {
    IconDownload, IconCheckOutline, IconCircle, IconCloseOutline
} from '@prototypeinteractive/jawwy-ui-kit-react';
import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { Helmet } from 'react-helmet';

import { docTypes } from '../../config/enums';

import {
    InCaseIssuesStyled,
    IconTerminateStyled,
    Description,
    ErrorMsg,
    Main,
    MobileNumber,
    ResendCode,
    ResendCodePlaceholder,
    ReturnLink,
    StyledOTPInput,
    SubErrorMsg,
    TerminationDescriptionStyled,
    TerminationDescriptionSecondStyled,
    TerminationResendCodeStyled,
    TerminationTitleStyled,
    TitleContainer,
    Title,
    WarningMessage
} from './styles';

const OtpVerification = ({
    altMobileNumber,
    docType,
    terminationMode,
    translation,
    mobileNumber,
    setActiveScreen,
    onChange,
    OTPStatus,
    OTPMessage,
    sendOtp,
    setOtpStatus
}) => {
    const [ showResendOTP, setShowResendOTP ] = useState(true);
    const [ otpResended, setOtpResended ] = useState(false);
    const [ otpInputKey, setOtpInputKey ] = useState(false);

    const translationFormatted = translation.secondPage ?? translation;

    const getTranslation = useCallback((type) => {
        switch (type) {
            case docTypes.QRCode:
                return translationFormatted.titleQR;
            case docTypes.EInvoice:
                return translationFormatted.titleEInvoice;
            default:
                return translationFormatted.title;
        }
    }, [ translationFormatted ]);

    return (
        <Main>
            <Helmet>
                <title>{ getTranslation(docType) }</title>
            </Helmet>
            {terminationMode ? (
                <>
                    <TerminationTitleStyled>
                        ****
                    </TerminationTitleStyled>
                    <TerminationDescriptionStyled>{translationFormatted.description}</TerminationDescriptionStyled>
                    <TerminationDescriptionSecondStyled>{translationFormatted.description2}</TerminationDescriptionSecondStyled>
                    <IconTerminateStyled />
                </>
            ) : (
                <>
                    <TitleContainer>
                        <IconDownload height={27} width={30} />
                        <Title>{ getTranslation(docType) }</Title>
                    </TitleContainer>
                    <Description>{translationFormatted.description}</Description>
                    <MobileNumber>
                        {
                            [ docTypes.QRCode, docTypes.EInvoice, docTypes.Device ].includes(docType) ?
                                altMobileNumber :
                                mobileNumber
                        }
                    </MobileNumber>
                </>
            )}

            {docType === docTypes.Contract && (
                <WarningMessage>
                    {translationFormatted.wrongNumber}
                    <ReturnLink onClick={() => { setOtpStatus('initial'); setActiveScreen(1); }}>
                        {translationFormatted.wrongNumberLink}
                    </ReturnLink>
                </WarningMessage>
            )}
            <StyledOTPInput disabled={[ 'wrong', 'correct' ].includes(OTPStatus)} key={otpInputKey} numDigits={4} onChange={onChange} />
            {!terminationMode && showResendOTP && (![ 'wrong', 'correct' ].includes(OTPStatus)) ? (
                <ResendCode onClick={() => { setShowResendOTP(false); sendOtp(); }}>
                    {translationFormatted.resendCode}
                </ResendCode>
            ) : (
                <ResendCodePlaceholder>&nbsp;</ResendCodePlaceholder>
            )}
            <ErrorMsg display={OTPMessage}>
                {translationFormatted.errors[OTPMessage]?.msg}
                <SubErrorMsg>{translationFormatted.errors[OTPMessage]?.sub_msg}</SubErrorMsg>
            </ErrorMsg>
            {OTPStatus === 'correct' && <IconCheckOutline height={100} width={100} />}
            {OTPStatus === 'initial' && <IconCircle height={100} width={100} />}
            {OTPStatus === 'wrong' && <IconCloseOutline height={100} width={100} />}
            {terminationMode && (
                <TerminationResendCodeStyled>
                    <span className='question'>{translationFormatted.resendQuestion}</span>
                    <button
                        className='resend'
                        type='button'
                        onClick={() => {
                            setShowResendOTP(false);
                            setOtpResended(true);
                            setOtpStatus('initial');
                            setOtpInputKey(!otpInputKey);
                            sendOtp();
                        }}
                    >
                        {translationFormatted.resend}
                    </button>
                </TerminationResendCodeStyled>
            )}
            {otpResended && <InCaseIssuesStyled dangerouslySetInnerHTML={{ __html: translationFormatted.tryAgainInCaseIssues }} /> }
        </Main>
    );
};

OtpVerification.propTypes = {
    altMobileNumber: PropTypes.string,
    docType: PropTypes.string,
    mobileNumber: PropTypes.string,
    onChange: PropTypes.func,
    OTPMessage: PropTypes.string,
    OTPStatus: PropTypes.string,
    sendOtp: PropTypes.func,
    setActiveScreen: PropTypes.func,
    setOtpStatus: PropTypes.func,
    terminationMode: PropTypes.bool,
    translation: PropTypes.shape({
        // eslint-disable-next-line react/forbid-prop-types
        secondPage: PropTypes.object
    })
};

OtpVerification.defaultProps = {
    altMobileNumber: null,
    docType: null,
    mobileNumber: null,
    onChange: null,
    OTPMessage: null,
    OTPStatus: null,
    sendOtp: null,
    setActiveScreen: null,
    setOtpStatus: null,
    terminationMode: false,
    translation: null
};

export default OtpVerification;
