import { IconDownload, IconCheck2 } from '@prototypeinteractive/jawwy-ui-kit-react';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';

import { docTypes } from '../../config/enums';
import { validateNationalId, validateMobileNumber } from '../../config/helpers';
import PhoneInput from '../phone-input-field';

import {
    Description, TitleContainer, Title, Main, StyledTextInput, ButtonStyled, ErrorMsg
} from './styles';

const DetailsVerification = ({
    altMobileNumber,
    apiResponse,
    docType,
    translation,
    mobileNumber,
    setMobileNumber,
    nationalId,
    setNationalId,
    onSubmit,
    errorMsg,
    isLoading
}) => {
    const [ isValidMobileNumber, setIsValidMobileNumber ] = useState(true);
    const [ isValidAltMobileNumber, setIsValidAltMobileNumber ] = useState(true);
    const [ isValidNationalId, setIsValidNationalId ] = useState(true);
    const [ isButtonActive, setIsButtonActive ] = useState(false);

    const getTranslation = useCallback((type, resource = 'title') => {
        switch (resource) {
            case 'title':
                switch (type) {
                    case docTypes.QRCode:
                        return translation.firstPage.titleQR;
                    case docTypes.EInvoice:
                        return translation.firstPage.titleEInvoice;
                    default:
                        return translation.firstPage.title;
                }
            case 'description':
                switch (type) {
                    case docTypes.QRCode:
                        return translation.firstPage.descriptionQR;
                    case docTypes.EInvoice:
                        return translation.firstPage.descriptionEInvoice;
                    default:
                        return translation.firstPage.description;
                }
            default:
                return '';
        }
    }, [ translation ]);

    useEffect(() => {
        const handler = (e) => {
            // prevent: "e", "=", ",", "-", "."
            if ([ 69, 187, 188, 189, 190 ].includes(e.keyCode))
                e.preventDefault();
        };

        document.getElementById('input-national-id')?.addEventListener('keydown', handler);
        document.getElementById('input-mobile-number')?.addEventListener('keydown', handler);
    }, []);

    useEffect(() => {
        if (mobileNumber)
            setIsValidMobileNumber(validateMobileNumber(mobileNumber));

        if (altMobileNumber)
            setIsValidAltMobileNumber(validateMobileNumber(altMobileNumber));

        if (nationalId)
            setIsValidNationalId(validateNationalId(nationalId));

        if (docType === docTypes.Contract) {
            if (nationalId && mobileNumber && isValidMobileNumber && isValidNationalId)
                setIsButtonActive(true);
            else
                setIsButtonActive(false);
        }

        if (docType === docTypes.Device) {
            if (nationalId && altMobileNumber && isValidAltMobileNumber && isValidNationalId)
                setIsButtonActive(true);
            else
                setIsButtonActive(false);
        }

        if (docType === docTypes.QRCode) {
            if (nationalId && mobileNumber && isValidMobileNumber && altMobileNumber && isValidAltMobileNumber && isValidNationalId)
                setIsButtonActive(true);
            else
                setIsButtonActive(false);
        }

        if (docType === docTypes.EInvoice) {
            if (mobileNumber) {
                if (nationalId && mobileNumber && isValidMobileNumber && altMobileNumber && isValidAltMobileNumber && isValidNationalId)
                    setIsButtonActive(true);
                else
                    setIsButtonActive(false);
            } else if (nationalId && altMobileNumber && isValidAltMobileNumber && isValidNationalId)
                setIsButtonActive(true);
            else
                setIsButtonActive(false);
        }
    }, [
        docType,
        isValidMobileNumber,
        isValidAltMobileNumber,
        isValidNationalId,
        mobileNumber,
        altMobileNumber,
        nationalId,
        apiResponse ]);

    return (
        <Main>
            <Helmet>
                <title>
                    { getTranslation(docType) }
                </title>
            </Helmet>
            <TitleContainer>
                <IconDownload height={27} width={30} />
                <Title>
                    { getTranslation(docType) }
                </Title>
            </TitleContainer>
            <Description>
                { getTranslation(docType, 'description') }
            </Description>
            <StyledTextInput
                appendContent={isValidNationalId && nationalId && !apiResponse?.error && <IconCheck2 height={25} width={22} />}
                error={apiResponse?.error ? translation.firstPage.nationalId.errors.api[apiResponse?.error] : undefined}
                focused={isValidNationalId && nationalId}
                id='input-national-id'
                label={translation.firstPage.nationalId.label}
                placeholder='0000000000'
                value={nationalId}
                preserveErrorSpace
                onChange={(e) => {
                    const value = e.target.value.replace(/[^0-9]/g, '').slice(0, 10);

                    setNationalId(value);
                }}
            />
            {([ docTypes.QRCode, docTypes.Contract ].includes(docType) || (mobileNumber && docType === docTypes.EInvoice)) && (
                <PhoneInput
                    appendContent={isValidMobileNumber && mobileNumber && !apiResponse?.error && <IconCheck2 height={25} width={22} />}
                    disabled={[ docTypes.QRCode, docTypes.Device, docTypes.EInvoice ].includes(docType)}
                    fieldMsg={{
                        message: apiResponse?.error
                            ?
                            translation.firstPage.mobileNumber.errors.api[apiResponse?.error]
                            :
                            ![ docTypes.QRCode, docTypes.EInvoice ].includes(docType) && translation.firstPage.mobileNumber.message,
                        isError: !!apiResponse?.error
                    }}
                    id='input-mobile-number'
                    label={translation.firstPage.mobileNumber.label}
                    value={mobileNumber}
                    onChange={(e) => {
                        const value = e.target.value.replace(/[^0-9]/g, '').slice(0, 9);

                        setMobileNumber(value);
                    }}
                />
            )}
            {[ docTypes.QRCode, docTypes.Device, docTypes.EInvoice ].includes(docType) && (
                <PhoneInput
                    appendContent={
                        isValidAltMobileNumber && altMobileNumber && !apiResponse?.error && <IconCheck2 height={25} width={22} />
                    }
                    fieldMsg={{
                        message: apiResponse?.error
                            ?
                            translation.firstPage.altMobileNumber.errors.api[apiResponse?.error]
                            :
                            translation.firstPage.altMobileNumber.message,
                        isError: !!apiResponse?.error
                    }}
                    label={translation.firstPage.altMobileNumber.label}
                    value={altMobileNumber}
                    disabled
                />
            )}
            <ErrorMsg display={errorMsg}>{translation.firstPage.errors[errorMsg]}</ErrorMsg>
            <ButtonStyled
                disabled={!isButtonActive || isLoading}
                onClick={onSubmit}
            >
                {translation.firstPage.buttonLabel}
            </ButtonStyled>
        </Main>
    );
};

DetailsVerification.propTypes = {
    altMobileNumber: PropTypes.string,
    apiResponse: PropTypes.shape({
        error: PropTypes.string
    }),
    docType: PropTypes.string,
    errorMsg: PropTypes.string,
    isLoading: PropTypes.bool,
    mobileNumber: PropTypes.string,
    nationalId: PropTypes.string,
    onSubmit: PropTypes.func,
    setMobileNumber: PropTypes.func,
    setNationalId: PropTypes.func,
    translation: PropTypes.shape({
        // eslint-disable-next-line react/forbid-prop-types
        firstPage: PropTypes.object
    })
};

DetailsVerification.defaultProps = {
    altMobileNumber: null,
    apiResponse: null,
    docType: null,
    errorMsg: null,
    isLoading: null,
    mobileNumber: null,
    nationalId: null,
    onSubmit: null,
    setMobileNumber: null,
    setNationalId: null,
    translation: null
};
export default DetailsVerification;
